"use client";

import { useState } from "react";
import Image from "next/image";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";

interface SatelliteImage {
  id: string;
  title: string;
  description: string;
  url: string;
  date: string;
  category: string;
  temperature: string;
  coordinates: { lat: number; lng: number };
}

interface SatelliteImageGridProps {
  images: SatelliteImage[];
}

export function SatelliteImageGrid({ images }: SatelliteImageGridProps) {
  const [selectedImage, setSelectedImage] = useState<SatelliteImage | null>(null);
  const [filter, setFilter] = useState<string>("all");

  const categories = ["all", "weather", "environment", "agriculture", "urban"];
  
  const filteredImages = filter === "all" 
    ? images 
    : images.filter(img => img.category === filter);

  return (
    <>
      <div className="space-y-6">
        <div className="flex flex-wrap gap-2">
          {categories.map((category) => (
            <Button
              key={category}
              variant={filter === category ? "default" : "outline"}
              size="sm"
              onClick={() => setFilter(category)}
              className="capitalize"
            >
              {category === "all" ? "Todos" : category}
            </Button>
          ))}
        </div>

        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredImages.map((image) => (
            <Card 
              key={image.id} 
              className="bg-slate-800 border-slate-700 overflow-hidden group cursor-pointer"
              onClick={() => setSelectedImage(image)}
            >
              <div className="relative h-48 overflow-hidden">
                <Image
                  src={image.url}
                  alt={image.title}
                  fill
                  className="object-cover group-hover:scale-110 transition-transform duration-300"
                />
                <div className="absolute inset-0 bg-gradient-to-t from-slate-900/80 to-transparent" />
                <div className="absolute top-4 left-4 flex gap-2">
                  <Badge variant="secondary">{image.category}</Badge>
                  <Badge variant="outline">{image.temperature}</Badge>
                </div>
              </div>
              <CardHeader>
                <CardTitle className="text-white">{image.title}</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-gray-400 text-sm">{image.description}</p>
                <p className="text-gray-500 text-xs mt-2">{image.date}</p>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>

      <Dialog open={selectedImage !== null} onOpenChange={() => setSelectedImage(null)}>
        <DialogContent className="max-w-4xl bg-slate-900 border-slate-700">
          <DialogHeader>
            <DialogTitle className="text-white">{selectedImage?.title}</DialogTitle>
          </DialogHeader>
          {selectedImage && (
            <div className="space-y-4">
              <div className="relative h-96">
                <Image
                  src={selectedImage.url}
                  alt={selectedImage.title}
                  fill
                  className="object-cover rounded-lg"
                />
              </div>
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <span className="text-gray-400">Data:</span>
                  <span className="text-white ml-2">{selectedImage.date}</span>
                </div>
                <div>
                  <span className="text-gray-400">Categoria:</span>
                  <span className="text-white ml-2 capitalize">{selectedImage.category}</span>
                </div>
                <div>
                  <span className="text-gray-400">Temperatura:</span>
                  <span className="text-white ml-2">{selectedImage.temperature}</span>
                </div>
                <div>
                  <span className="text-gray-400">Coordenadas:</span>
                  <span className="text-white ml-2">
                    {selectedImage.coordinates.lat.toFixed(4)}, {selectedImage.coordinates.lng.toFixed(4)}
                  </span>
                </div>
              </div>
              <p className="text-gray-300">{selectedImage.description}</p>
            </div>
          )}
        </DialogContent>
      </Dialog>
    </>
  );
}
