"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { TrendingUp, TrendingDown, Minus } from "lucide-react";

interface ClimateMetricsProps {
  temperature: number;
  humidity: number;
  precipitation: number;
  windSpeed: number;
}

export function ClimateMetrics({ temperature, humidity, precipitation, windSpeed }: ClimateMetricsProps) {
  const metrics = [
    {
      label: "Temperatura",
      value: `${temperature}°C`,
      trend: temperature > 25 ? "up" : temperature < 15 ? "down" : "stable",
      color: "text-orange-400",
      bgColor: "bg-orange-400/10"
    },
    {
      label: "Umidade",
      value: `${humidity}%`,
      trend: humidity > 70 ? "up" : humidity < 30 ? "down" : "stable",
      color: "text-blue-400",
      bgColor: "bg-blue-400/10"
    },
    {
      label: "Precipitação",
      value: `${precipitation}mm`,
      trend: precipitation > 10 ? "up" : precipitation === 0 ? "down" : "stable",
      color: "text-cyan-400",
      bgColor: "bg-cyan-400/10"
    },
    {
      label: "Velocidade do Vento",
      value: `${windSpeed}km/h`,
      trend: windSpeed > 20 ? "up" : windSpeed < 5 ? "down" : "stable",
      color: "text-green-400",
      bgColor: "bg-green-400/10"
    }
  ];

  const TrendIcon = ({ trend }: { trend: string }) => {
    switch (trend) {
      case "up":
        return <TrendingUp className="w-4 h-4 text-red-400" />;
      case "down":
        return <TrendingDown className="w-4 h-4 text-blue-400" />;
      default:
        return <Minus className="w-4 h-4 text-gray-400" />;
    }
  };

  return (
    <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
      {metrics.map((metric, index) => (
        <Card key={index} className="bg-slate-800/50 border-slate-700">
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-gray-400">
              {metric.label}
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center justify-between">
              <span className={`text-2xl font-bold ${metric.color}`}>
                {metric.value}
              </span>
              <TrendIcon trend={metric.trend} />
            </div>
            <div className={`mt-2 h-1 rounded-full ${metric.bgColor}`} />
          </CardContent>
        </Card>
      ))}
    </div>
  );
}
